body {
  font-family: sans-serif;
}
.main {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.header {
  height: 240px; /* 3 */
  background-color: dodgerblue;
  display: flex;
  justify-content: center;
  align-items: center;
}
.aboutus {
  /* height: 400; */
  /* background-color: gray; */
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: 25px 200px 25px 200px;
  margin-top: 20px;
}

.abt {
  font-family: helvetica;
  font-weight: bold;
  font-size: 49px;
  color: #201453;
}
.abttext {
  /* font-family:proxima */
  text-align: center;
  font-size: 16px;
  font-family: proxima-nova, sans-serif;
  font-weight: 400;
  font-style: normal;
}
.box {
  width: 500px;
  padding: 20px;
}
.logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: -30px;
}
.name {
  font-family: Helvetica;
  font-size: 90px;
  font-weight: bold;
  color: white;
  margin-left: 12px;
}
.subtitle {
  font-family: helvetica;
  font-size: 25px;
  color: white;
}

.redirect {
  font-family: helvetica;
  font-size: 25px;
  color: white;
  font-weight: bold;
  text-decoration: underline;
}
.people {
  height: auto;
  display: flex;
}
.people .left,
.people .right {
  flex: 1;
  /* background: green; */
}
.people .center {
  flex: 3;
}
.center {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.Profile {
  display: grid;
  grid-template-rows: 4fr 1fr 1fr;
  margin: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 400px;
  /* background-color:thistle; */
}
.pfp {
  border-radius: 300px;
  background-color: gray;
  width: 225px;
  height: 225px;
  object-fit: cover;
  object-position: 50% 50%;
}
.pfpname {
  font-size: 27px;
  font-family: playfair-display, serif;
  font-weight: 400;
  font-style: normal;
  margin-top: 20px;
}
.description {
  font-size: 13px;
  font-family: helvetica;
  margin-top: 10px;
}
